  checkphone(phonein)
  {

		var phone_sav = phonein.replace(/[^\d]/g, '');;
		var output = '';

        for (var i = 0; i < phone_sav.length; i++){

			var curstr = phone_sav[i];
			if (phone_sav[0]=="3")
			{
	            //Belarus
	            //+375(29)898-66-00
	            if (i == 0){
	                output += "+";
	            } else if (i == 3){
	                output += ' (';
	            } else if (i == 5){
	                output += ') ';
	            } else if (i == 8){
	                output += "-";
	            } else if (i == 10) {
	            output += "-";
	            }else if (i == 12){
	                break;
	            } ;
	        } else
	        {
	            //Russia
	            if (i == 0){
	                output += "+";
	            } else if (i == 1){
	                output += ' (';
	            } else if (i == 4){
	                output += ') ';
	            } else if (i == 7){
	                output += "-";
	            } else if (i == 9) {
	            output += "-";
	            }else if (i == 11){
	                break;
	            } ;
	        }
            output += curstr;
        };
        return output;


  }



