handleToArhiv(order,event)
  {
	const self = this;


	var datain = {
	    action: "OrderToArhiv",
	    orderid: order,
	    vkid: this.state.vkid,
	};

	var data = new FormData();
	data.append( "json", JSON.stringify( datain ) )

	fetch('https://zremont.com/order/ajax.php',{
		    method: 'POST',
		    body: data
		  })
        .then(response => response.json())
		.then(function(data)
		{

			if (data.success=="Y")
			{
				//document.getElementById(data.orderid).style.display = "none";
				self.setState({ popout:
				      <Alert
				        actions={[{
				          title: 'OK',
				          autoclose: true,
				        }]}
				        onClose={self.closePopout}
				      >
				        <h2>Ваш заказ успешно отправлен в архив!</h2>
				      </Alert>
				    });


				let order_list = self.state.order_list;

				order_list.map((value,i)=>
					{
						if (value.id==data.orderid) self.state.order_list.splice(i,1);
					}
				);
				self.setState({order_list: order_list});

			}
			else
			{

				self.setState({ popout:
				      <Alert
				        actions={[{
				          title: 'OK',
				          autoclose: true,
				        }]}
				        onClose={self.closePopout}
				      >
				        <h2>Ошибка архивирования заказа:</h2>
				        <h2>{data.errors}</h2>
				      </Alert>
				    });

			}
	  	}
     );

  //setTimeout(function() { document.location.reload(true);}, 3000);
  }

